:root {
  font-size: 10px;
}

.body {
  font-family: "Rum Raisin", Arial, sans-serif;
  background: url("../img/background.jpg") no-repeat center center fixed;
  width: 100%;
  height: 100%;
  background-size: 100% 100%;
  background-repeat: repeat-x;
  letter-spacing: 2px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main {
  margin-top: 50px;
}

.header {
  background-color: white;
  font-size: 42px;
  text-align: center;
  @media (max-width: 720px) {
    font-size: 27px;
  }
}

.buttons {
  display: flex;
  justify-content: space-between;
  margin: 10px 20px 0;
  @media (max-width: 720px) {
    margin-top: 20px;
  }
}

.button {
  border: none;
  cursor: pointer;
  font-size: 37px;
  transition: background-color 0.2s;
  @media (max-width: 720px) {
    font-size: 22px;
  }
}

.button--yes:hover {
  background-color: rgb(21, 223, 156);
}

.button--yes:active {
  background-color: rgb(42, 155, 117);
}

.button--no {
  transition: top 0.5s ease-in-out, left 0.5s ease-in-out;
}

.button--no:hover {
  background-color: rgb(204, 24, 11);
}

.button--no:active {
  background-color: rgb(94, 14, 9);
}

.picture {
  margin: 13px auto 0;
  display: block;
  width: 300px;
  height: 300px;
  background-image: url("../img/toChoose.gif");
  background-size: cover;
  @media (max-width: 720px) {
    margin-top: 37px;
    width: 200px;
    height: 200px;
  }
}

.text {
  display: block;
  margin-top: 15px;
  text-align: center;
  font-size: 27px;
  opacity: 0;
  transition: opacity 5s;
  @media (max-width: 720px) {
    font-size: 22px;
  }
}
